@import url(https://fonts.googleapis.com/css2?family=Press+Start+2P&display=swap);
html {
  font-family: 'Press Start 2P', 'sans-serif';
}

body {
  margin: 0;
  color:white;
  overflow-x: hidden;
}

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  line-height: 150%;
}

/* *:hover {
  cursor:default
} */

html {
  height: 100%;
}

h1, h2, h3, h4, h5, h6 {
  font-size: 100%;
  margin: 0 0 0 0;
  font-weight: lighter;
}

input, select, option, optgroup, textarea, button,
pre, code {
  font-size: 100%;
  font-family: inherit;
}

ol, ul {
  list-style: none;
}

button {
  background: none;
  border: none;
  padding: 0;
}


